import { Component, OnInit } from '@angular/core';
import { MobileService } from 'src/app/_services/mobile.service';

@Component({
  selector: 'mobile-list-app',
  templateUrl: './mobile-list-app.component.html',
  styleUrls: ['./mobile-list-app.component.css']
})
export class MobileListAppComponent implements OnInit {

   mobileList = [];

  constructor(private _mobileService: MobileService) { }

  delete(id:number):void{
    console.log(id);
    this.mobileList.splice(id,1);
  }

  public filterList(choice: number) {
    console.log(choice);
    if (this.mobileList) {
      switch (choice) {
        case 1:
          this.mobileList.sort((obj1, obj2) => { return obj1.mobId - obj2.mobId });
          break;
        case 2:
          this.mobileList.sort((obj1, obj2) => { return obj1.mobName.localeCompare(obj2.mobName) });
          break;
        case 3:
          this.mobileList.sort((obj1, obj2) => { return obj1.mobPrice - obj2.mobPrice });
          break;
        default: break;
      }
    }
  }
  ngOnInit(): void {
    this._mobileService.getBook()
      .subscribe(mobile => this.mobileList = mobile);
  }

}
